
*{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.container {
  max-width: 600px;
  margin: 0 auto;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 60px;
  font-size: 24px;
}


h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

.todo-div{
  background-color: #9ad3bc;
  padding: 25px;
  border-radius: 30px;
}
.todo-input {
  width: 100%;
  padding: 10px 18px;
  font-size: 18px;
  margin-bottom: 16px;
  border-radius: 20px;
}
.todo-item {
  margin-bottom: 12px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
}
.remove-item {
  height: 25px;
  width: 25px;
  padding: 0 0 5px 5px;
  cursor: pointer;
  margin-left: 14px;
  border: 1px solid white;
  border-radius: 50%;
}
.todo-item-label {
  padding: 10px;
  border: 1px solid white;
  margin-left: 12px;
  border-radius: 20px;
}
.todo-item-edit {
  font-size: 24px;
  color: #2c3e50;
  margin-left: 12px;
  width: 100%;
  padding: 10px;
  border-radius: 20px;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
}
.completed {
  text-decoration: line-through;
  color: grey;
}
.extra-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  font-size: 16px;
  border-top: 1px solid lightgrey;
  padding-top: 14px;
  margin-bottom: 14px;
}
button {
  font-size: 14px;
  background-color: white;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border-radius: 20px;
}
.active {
  background: lightgreen;
}
/* CSS Transitions*/
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity .2s;
  transition: opacity .2s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
